import { Component, Input, ElementRef } from '@angular/core';
import { LoadingStatusService } from '../loadingstatus.service';

import * as _ from 'lodash';


@Component({
  selector: 'app-frame',
  template: require('./frame.component.html'),
  styles: [require('./frame.component.css')]
})

export class FrameComponent {
  @Input() srcdoc: string;
  @Input() targetUrl: string;
  @Input() useFontSlices: boolean;

  show: boolean = true;

  observer: any;
  startTimestamp: number;
  timeLoading: number;

  constructor(
    private elRef:ElementRef,
    private loadStatusService: LoadingStatusService
  ) {}

  ngAfterViewInit() {
    this.observer = new MutationObserver(mutation => {
      mutation.forEach(m => {
        this.timeLoading = 0;
        this.startTimestamp = Date.now();
        console.log("Start loading.");
      });
    }).observe(this.elRef.nativeElement, {
      attributes: true,
      attributeFilter: ['srcdoc'],
      subtree: true
    });
    this.elRef.nativeElement.querySelector('iframe').onload = () => {
      let myFirstPromise = new Promise((resolve, reject) => {
        this.elRef.nativeElement.querySelector('iframe').contentDocument.fonts.ready.then((fontFaceSet) => {
          if(fontFaceSet.check('12px Noto Sans SC') || fontFaceSet.check('12px Noto Sans SC Sliced') ) {
            this.onPageLoaded(resolve);
          } else {
            let timer = setInterval(()=> {
              if(fontFaceSet.check('12px Noto Sans SC') || fontFaceSet.check('12px Noto Sans SC Sliced') ) {
                this.onPageLoaded(resolve);
                clearInterval(timer);
              }
            }, 100);
          }
        });
      });
      myFirstPromise.then(function(successMessage){
        console.log(successMessage);
      })
    };
  }

  ngDestroy() {
    this.observer.disconnect();
  }

  onPageLoaded(resolve) {
    console.log('Font loaded.');
    this.timeLoading = (Date.now() - this.startTimestamp) / 1000;
    if (this.useFontSlices) {
      this.loadStatusService.loadNextFrame$.emit();
    }
    resolve('DOM loaded.');
  }
}



