import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import async from 'async';

@Injectable()
export class ContentService {
  constructor(private http: Http) {}

  getContent(url: string) {
    return new Promise<any>((resolve, reject) => {
      async.parallel({
        fontSlice: (callback) => {
          let fontSliceUrl = '/frame?url=' + url + '&slice=True';
          this.http.get(fontSliceUrl)
            .toPromise()
            .then(response => {
              callback(null, response["_body"]);
            })
            .catch((err) => {
              callback(err);
            });
        },
        nativeFont: (callback) => {
          let nativeFontUrl = '/frame?url=' + url + '&slice=False';
          this.http.get(nativeFontUrl)
            .toPromise()
            .then(response => {
              callback(null, response["_body"]);
            })
            .catch((err) => {
              callback(err);
            });
        }
      },
      // optional callback
      (err, results) => {
        if(err) {
          console.log(err);
          reject(err);
        } else {
          resolve(results);
        }
      });
    });
  }
}



