import { Component } from '@angular/core';

import { ContentService } from './content.service';
import { LoadingStatusService } from './loadingstatus.service';

import * as _ from 'lodash';

@Component({
  selector: 'my-app',
  template: require('./app.component.html'),
  styles: [require('./app.component.css')]
})

export class AppComponent {
  urlInput: string = '';
  targetUrl: string;
  targetDOM: any = {};
  nativeFontDOM: string = '';
  fontSlicesDOM: string = '';

  constructor(
    private contentService: ContentService,
    private loadStatusService: LoadingStatusService
  ) {}

  ngOnInit() {
    this.loadStatusService.loadNextFrame$.subscribe(()=> {
      if (typeof(this.targetDOM.nativeFont) !== 'undefined') {
        console.log("Load next frame.");
        this.nativeFontDOM = this.targetDOM.nativeFont;
      }
    });
  }

  setUrl(url:string) {
    if (typeof(url) === 'undefined') {
      url = this.urlInput;
    }
    else {
      this.urlInput = url;
    }
    if (!_.startsWith(url, 'http')) {
      url = 'http://' + url;
    }
    this.targetUrl = decodeURIComponent(url.replace(/\+/g,  " "));
    this.contentService.getContent(url).then(results => {
      this.targetDOM = results;
      console.log("Load first frame.")
      this.fontSlicesDOM = this.targetDOM.fontSlice;
    });
  }
}





